import lvSelect from '@/components/lv-select/lv-select.vue';
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue';
import groupItem from '@/components/group/group-item.vue';
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
import filterBar from '@/components/uni-compus-components/uniCompus-filter.vue';
import uniCompusButton from '@/components/uni-compus-components/unicompus-button.vue';
import goDetail from '@/components/uni-compus-components/uniCompus-goDetail.vue';
import msDropdownMenu from '@/components/ms-dropdown/dropdown-menu.vue';
import msDropdownItem from '@/components/ms-dropdown/dropdown-item.vue';

import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			loading: false,
			showValue: 'name', // 需要显示的数据，必须与infoList中的name对应
			searchValue: '',
			infoList: [],
			infoLists: [
					{
						name: '吕星辰1'
					},
					{
						name: '吕星辰2'
					},
					{
						name: '吕星辰3'
					},
					{
						name: '吕星辰4'
					}
			],

			// 下拉刷新的配置
			downOption: {

			},
			// 上拉加载的常用配置
			upOption: {

			},

			list: [
				{
					text: "上海海事大学",
					value: 0
				},
				{
					text: "上海交通大学",
					value: 1
				},
				{
					text: "复旦大学",
					value: 2
				}
			],
			school: "上海海事大学",

			filters: [
				{
					title: '兴趣爱好',

					choices: ['舞蹈', '电竞', '书法', '书法']
				},
				{
					title: '兴趣爱好',

					choices: ['舞蹈', '电竞', '书法', '书法']
				},
				{
					title: '兴趣爱好',

					choices: ['舞蹈', '电竞', '书法', '书法']
				}
			],
			swiperHeight: 0,
			tapIndex: 0,
			user: {
				groupInfo: {
					prepare: 0,
					already: 0
				}
			},
			groupDetail: [
				{
					content: [
						{
							groupName: '轻松一校项目组',
							groupLogo: '../../static/test/waterfull/1.jpg',
							intro: '轻松一校项目组是地球上最强大的组织之一，它负责...',
							tag: ['强大', '优秀']
						}
					]
				}
			]
		};
	},
	components: {
		uniSearchBar,
		groupItem,
		uniDrawer,
		filterBar,
		uniCompusButton,
		lvSelect,
		goDetail,
		msDropdownMenu,
		msDropdownItem
	},
	methods: {
		/*下拉刷新的回调*/
		downCallback(){
			// 与 mescroll-body 的处理方式一致 >
		},
		/*上拉加载的回调*/
		upCallback(page) {
			// 与 mescroll-body 的处理方式一致 >
		},

		gotoDetail(){
			uni.navigateTo({
				url:"/pages/groupDetail/groupDetail"
			})
		},
		checkAlreadyJoin() {
			let item = { type: 2 };
			uni.navigateTo({
				url: `/pages/mySave/mySave?item=${encodeURIComponent(JSON.stringify(item))}`
			});
		},
		checkAlreadySave() {
			let item = { type: 1 };
			uni.navigateTo({
				url: `/pages/mySave/mySave?item=${encodeURIComponent(JSON.stringify(item))}`
			});
		},
		handleSearch() {
			this.loading = true;
			setTimeout(() => {
				this.loading = false;
				this.infoList = this.infoLists;
			}, 2000);
		},
		change(val) {
			console.log(val);
		},
		tap(index) {
			this.tapIndex = index;
		},
		tabChange(e) {
			this.tapIndex = e.detail.current;
		},
		openFilter() {
			this.$refs.uniDrawer.open();
		},
		closeDrawer() {
			this.$refs.uniDrawer.close();
		}
	},
	async onLoad() {
		let swiperHeight = uni.getSystemInfo({
			success: res => {
				let height = res.windowHeight - uni.upx2px(230);
				this.swiperHeight = height;
			}
		});
		let res = await this.request('groups/');
		console.log(res[1].data.data.records);
	}
};
